// footer styles

.site-footer {
	background-color: $blue;

	&__meta {
		color: $red;
		padding-top: 15px;
		padding-bottom: 15px;
		box-sizing: border-box;
		@include respond-to-min($mq-med){
			text-align: left;
		}
	}

	&__meta-item {
		font-style: normal;
		font-family: $f-family-1;
		font-size: $f-size-footer-meta;
		letter-spacing: 2px;
		margin-bottom: 5px;

		@include respond-to-min($mq-med){
			display: inline-block;
			vertical-align: middle;
			margin-right: 10px;

			&:after {
				content: '-';
				margin-left: 10px;
			}
		}
		
		&:last-child {
			&:after {
				content: '';
			}
		}
	}
}
