// typography font family etc...



$presurraMonoRegular: 'gt-pressura-mono-regular-webfont';

@font-face {
    font-family: 'pressura_mono_regular';
    src: url($fontUrl + $presurraMonoRegular + '.eot');
    src: url($fontUrl + $presurraMonoRegular + '.eot?#iefix') format('embedded-opentype'),
         url($fontUrl + $presurraMonoRegular + '.woff2') format('woff2'),
         url($fontUrl + $presurraMonoRegular + '.woff') format('woff'),
         url($fontUrl + $presurraMonoRegular + '.ttf') format('truetype'),
         url($fontUrl + $presurraMonoRegular + '.svg#pressura_mono_regular') format('svg');
        
    font-weight: normal;
    font-style: normal;

}


