//intro-banner styles

.intro-banner {
	overflow: hidden;
	max-width: $mq-max-large;
	margin: 0 auto;

	@include respond-to-min(750px) {
	    margin-bottom: -135px;
	}
}

.intro-banner__image {
	width: 480px;

	@include respond-min-max($mq-small, $mq-med) {
		width: 750px;
	}

	@include respond-to-min($mq-med) {
		width: 100%;
		margin: 0 auto;
	}
}
