//contact-details

.contact-details--footer {
	padding: 30px 0 20px;
	background-color: $c-primary-1;
}

.contact-details__wrapper--footer {
	@include wrapper;
}

.address__item {
	display: block;
	font-style: normal;

}
.address__item-identifier {
	display: inline-block;
	width: 10px;
	text-align: center;
}

.address__item--footer {
	color: $c-primary-2;
	font-family: $f-family-1;
	font-size: $f-size-footer;
	letter-spacing: 2px;
	text-transform: uppercase;
	margin-bottom: 5px;
}

.address__postal--footer {
	@include respond-to-min($mq-med){
		display: inline-block;
		vertical-align: top;
		margin-right: 80px;
	}
}

.address__digital--footer {
	@include respond-to-min($mq-med){
		display: inline-block;
		vertical-align: top;
	}
}

.address__website--footer {
	margin-top: 30px;
}
