//navigation primary

$item-padding: 	10px;

.navigation-primary {
	float: right;
	display: inline-block;
	vertical-align: bottom;
}

.navigation-primary__item {
	display: inline-block;
	padding: 10px $item-padding 0;
	text-transform: uppercase;
	font-family: $f-family-1;
	font-size: 14px;
	position: relative;
	&:last-child{
		padding-right: 0;
	}

	&.is-active, &:hover {
		text-decoration: none;
		&:after {
			content: '';
			display: block;
			position: absolute;
			left: $item-padding;
			bottom: -2px;
			height: 2px;
			width: calc(100% - 20px);
			background-color: $c-primary-2;

		}
		&:last-child:after{
			width: calc(100% - 10px);
		}
	}
}

//theme settings

.navigation-primary__item--blog {
	color: $theme-1-primary-1;
	&.is-active, &:hover {
		text-decoration: none;
		&:after {
			background-color: $theme-1-primary-1;
		}
	}
}

.navigation-primary__item--jobs {
	color: $theme-2-primary-1;
	&.is-active, &:hover {
		text-decoration: none;
		&:after {
			background-color: $theme-2-primary-1;
		}
	}
}
