//wysiwyg editor styles for blog

.wysiwyg-content {
	//nested styles are needed here as there is no control over class names in the articles.

	h2 {
		padding-top: $d-type-spacing;
		@include typeSpacing;
	}

	h3 {
		padding-top: $d-type-spacing;
		@include typeSpacing;
	}

	ul {
		margin: $d-type-spacing 20px 5px;
	}

	li {
		margin: $d-type-spacing 0;
	}

	p a {
		font-weight: 400;
		word-wrap: break-word;
	}

	p img {
		width: calc(100% + #{$d-wrapper-padding});
		position: relative;
		left: calc(-#{$d-wrapper-padding} - 2px);

		@include respond-to-min($mq-small){
			right: calc(-15px - #{$d-wrapper-padding--sm});
		}
		@include respond-to-min($mq-large){
			right: 0;
		}
	}

	p strong {
		font-weight: normal;
		font-family: $f-family-4;
	}
	iframe {
		width: 100%;
	}
}
