//article image


.article__image-container {
	display: inline-block;
	vertical-align: top;
	width: calc(100% + #{$d-wrapper-padding+1px});
	margin-right: -#{$d-wrapper-padding+1px};
	margin-bottom: $d-type-spacing;
	@include respond-to-min($mq-small){
		width: calc(100% + #{$d-wrapper-padding--sm+2px});
		margin-right: -#{$d-wrapper-padding--sm+2px};
	}
	@include respond-to-min($mq-large){
		width: 49%;
		margin-right: 0;
		float: right;
	}
}

.article__copy {
	display: inline-block;
	vertical-align: top;
	@include respond-to-min($mq-large){
		width: 49%;
	}
}