//Layout - styles for all layout and or grid system

.wrapper {
	@include wrapper;
}

.grid {
	@include clearfix;
	margin-left: -10px;
}
.grid__row {
	margin-bottom: $row-divider;
	@include clearfix;
}
.col {
	box-sizing: border-box;
	float: left;
	min-height: 1px;
	padding-left: 10px;
	width: 100%;
	margin-bottom: 10px;
	@include respond-to-min($mq-med){
		margin-bottom: 0;
	}
}

.col--1\/3--promo-grid ,
.col--2\/3--promo-grid {
	width: 50%;
}

@include respond-to-min($mq-med){
	@include widths(12);
}
