//typography -line height settings.

$fontUrl:						'/_client/fonts/';
$base-line-height:				1.4;

$f-line-height-headings: 		1.2;

$f-family-1:					'pressura_mono_regular', 'sans serif', 'arial';
$f-family-2:					'calibre-light', 'sans-serif';
$f-family-3:					'noe-book', Georgia, Times New Roman, serif;
$f-family-4:					'calibre-medium', 'sans-serif';

$f-size-body:					18PX; //PX needs to be in capitals, will be ignored by pxtorem
$f-size-footer:					12px;
$f-size-footer-meta:			9px;

$f-family-headings:				$f-family-3;
$f-family-body:					$f-family-2;
