//data-list-item - styles

$d-data-list-divider: 40px;

.data-list {
	padding-top: $d-data-list-divider;
}

.data-list__copy {
	@include respond-to-min($mq-large) {
		max-width: $d-article-max;
	}
}

.data-list__item {
	margin-bottom: $d-data-list-divider;

	&:last-child {
		margin-bottom: 0;
	}
}

.data-list__item-link {
	display: block;
	&:hover {
		text-decoration: none;
	}
}

.data-list__item-title {
	color: $c-data-list-title;
	font-size: 24px;
	margin-bottom: 10px;
	font-weight: normal;
}

.data-list__item-intro {
	color: $c-primary-1;
}


.data-list__item--header {
	margin-bottom: 0;
}
