//article - wysiwyg editor styles for api content

.article__content {
	padding-top: 15px;
	padding-bottom: 15px;
    position: relative;
    z-index: 1;

    @include respond-to-min($mq-med){
		padding-top: 50px;
		padding-bottom: 50px;
    }
}

.article-blog-container {
	background-color: $green-light;
}

.article-blog-container__inner--blog-teaser {
	@include respond-to-min($mq-med){
		display: table;
		width: 100%;
		max-width: $mq-max;
		margin: 0 auto;
		padding-left: $d-wrapper-padding;
		padding-right: $d-wrapper-padding;
	}
}
