//Component - meta contructor - styles for the meta constructor.

$border-color: 			$c-primary-1;
$border-color-header:	$c-secondary-5;

.meta-constructor {
	border: 0;
	border-bottom: 2px;
	border-color: $border-color;
	border-style: solid;
	letter-spacing: -4px;
	width: 100%;
	margin-right: -$d-wrapper-padding;
	margin-bottom: $d-type-spacing;
	
	@include respond-to-min($mq-large){
		margin-right: 0;
	}
}

.meta-constructor__item {
	display: inline-block;
	letter-spacing: 1px;
	padding: 8px 0 8px 10px;
	font-family: $f-family-1;
	font-size: 10px;
	text-transform: uppercase;
	color: $c-primary-1;
	font-weight: bold;

	&:not(&:last-child) {
		border: 0;
		border-right: 2px;
		border-color: $border-color;
		border-style: solid;
		padding-right: 10px;
	}

	&:empty {
		display: none;
		border: none;
		+ :nth-child(2) {
			padding-left: 0;
		}
	}

	&:first-child {
		padding-left: 0;
	}
}

.meta-constructor__author {
	font-style: normal;
	margin-right: 5px;
	&:last-child {
		margin-right: 0;
	}
}
