//_elements - general
html, body {
	height: 100%;
	line-height: $base-line-height;
	color: $c-body;
	font-family: $f-family-body;
	font-size: $f-size-body;
	font-weight: 300;
}

p {
	@include typeSpacing;
	&:empty{
		display: none;
	}
}

h1,h2 {
	font-family: $f-family-headings;
	line-height: $f-line-height-headings;
	letter-spacing: -1px;
}
