//Styleguide layout and filler styles.

.styleguide__grid-item {
	height: 50px;
	background: $black;
	width: 100%;
	vertical-align: middle;
	text-align: center;
	color: $white;
}

.styleguide__section {
	margin-bottom: 40px;
}

.styleguide__section-title {
	margin: 30px 0;
	padding-bottom: 10px;
	border-bottom: 1px solid $black;
}

.styleguide__btn-section {
	a {
		margin: 10px;
	}
}

$c-color-pallet-bg: lighten($black, 90%);

.color-list {
	background: $c-color-pallet-bg;
	padding-left: 1%;
}

.color-item {
	display: inline-block;
	padding: 10px;
	width: 32%;

	@include respond-to-min($mq-med) {
		width: 24%;
	}	
}

.color-item__color {
	border-radius: 3px 3px 0 0;
	display: block;
	height: 60px;
}

.color-item__title {
	background: $white;
	border-radius: 0 0 3px 3px;
	display: block;
	padding: 10px 5px;
	width: 100%;
}
