//Header - styles for header

.site-header {
	padding-bottom: 25px;
	padding-top: 25px;
	position: relative;
}
.site-header__logo {
	width: 95px;
	height: 34px;
	display: inline-block;
	vertical-align: bottom;
	color: $c-primary-1;
	
	& g { 
		fill: currentColor; 
	}
}

.site-header--title {
	font-size: 34px;
}

//theme settings

.site-header--blog {
	background-color: $theme-1-site-heading-bg;
}

.site-header--jobs {
	background-color: $theme-2-site-heading-bg;
}
