//view more link

.link-container--blog-teaser {
	background-color: $theme-1-site-container-dark;
	padding: 25px 0;
	@include respond-to-min($mq-med){
		display: table-cell;
		text-align: center;
		vertical-align: middle;
	}
}


.view-more-link {
	color: $c-primary-1;
	display: block;
	&:hover {
		text-decoration: none;
		color: $white;
		fill: currentColor;
	}
}

.view-more-link__text {
	display: inline-block;
	vertical-align: middle;
}
.view-more-link__icon {
	display: inline-block;
	vertical-align: middle;
	width: 34px;
	height: 28px;
	margin-right: 10px;
	
}
.view-more-link--blog-teaser{
	text-align: center;
}
.view-more-link__text--blog-teaser {
	
	@include respond-to-min($mq-med){
		max-width: 80px;
		display: block;
		margin: 0 auto;
	}
}

.view-more-link__icon--blog-teaser {
	@include respond-to-min($mq-med){
		margin-right: 0;
		margin-bottom: remify($d-type-spacing);
	}
}