//Page data - styles for the heading of the page

.page-data {
	background-color: $offWhite;
}

.page-data__title {
	font-family: $f-family-1;
	margin-bottom: 15px;
	font-weight: normal;
	font-size: 12px;
	letter-spacing: 0;
	word-wrap: break-word;
}

.page-data__title-intro {
	font-size: 12px;
	font-family: $f-family-1;
	display: block;
	text-transform: uppercase;
	margin-bottom: 10px;
}

.page-data__intro {
	font-size: $f-size-body;
	font-family: $f-family-2;
	max-width: $d-article-max;
}

.page-data__title--flush {
	margin-bottom: -33px;
}

.page-data__title--large {
	font-size: 32px;
}

.page-data__title--larger {
	font-size: 36px;
}

.page-data__title--heading {
	font-family: $f-family-3;
}

.page-data__title--uppercase {
	text-transform: uppercase;
}

.page-data__title--flush.page-data__title--large {
	margin-bottom: -34px;
}

//theme settings

/* BLOG TEASER */
.page-data--blog-teaser {
	padding: 0;
}

/* BLOG */
.page-data--blog {
	background-color: $theme-1-page-data-bg;
}
.page-data__title--blog {
	color: $theme-1-primary-1;
}
.page-data__title-intro--blog {
	color: $theme-1-primary-1;
}

/* JOBS */
.page-data--jobs {
	background-color: $theme-2-page-data-bg;
}
.page-data__title--jobs {
	color: $theme-2-primary-1;
}

.page-data .article__content {
    background-color: $offWhite;
}

.page-data--blog .article__content {
    background-color: $theme-1-page-data-bg;
}

.page-data--jobs .article__content {
    background-color: $theme-2-page-data-bg;
}
